* {
  font-family: Arial, sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

body > div {
  background-color: #f0f0f0;
  padding: 5vh;
  font-size: 80%;
}

h1 {
  margin-top: 2em;
}

div #uploadCsv {
  margin: 1em 0;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

th, td {
  border: 1px solid #ddd;
  padding: 12px;
}

th {
  color: #fff;
  background-color: #346336;
  font-weight: bold;
}

table#csvMeta {
  width: auto;
}

table#csvMeta th {
  text-align: right;
}

table#csvMeta td {
  text-align: left;
}

table#csv div.canvasHolder {
  aspect-ratio: 1;
  width: 100%;
  max-width: 10vw;
  height: auto;
  max-height: 10vw;
  margin: auto;
}

table {
  width: 100%;
}

td.split {
  background-color: #5f1d1d;
  border: none;
  padding: 0;
  padding: 5px !important;
}

tr.daySeparator td {
  text-align: left;
  background-color: #f0f0f0;
}

td {
  text-align: right;
}

table#intervals th, table#intervals td {
  padding-top: 5px;
  padding-bottom: 5px;
}

table#intervals th.error {
  background-color: red;
}

table#intervals th.insufficient {
  background-color: #8cb344;
}

ul#warnings {
  color: #fff;
  background-color: red;
  height: 20vh;
  margin: 0;
  font-size: 80%;
  display: none;
  overflow-y: scroll;
}

th.distribution, th.consumer {
  word-break: break-word;
}

tr.csvHeaderRow input {
  width: 100%;
  min-width: 0;
}
